.payment {
  background-color: white;
  border: 4px solid red;
}
.payment > h1 {
  border: 1px solid red;
  text-align: center;
  padding: 10px;
  font-weight: 400;
  background-color: rgb(234, 237, 237);
  border-bottom: 1px solid lightgray;
}
.payment_countItem {
  text-decoration: none;
}
.payment_section {
  border: 1px solid red;
  display: flex;
}
.payment_title {
  flex: 0.25;
}
.payment_title > h3 {
  font-weight: 500;
}
.payment_address,
.payment_items,
.payment_details {
  flex: 0.75;
}
.payment_details > form {
  border: 2px solid green;
  max-width: 400px;
}
.payment_title > h3 {
  padding-bottom: 20px;
}

.payment_button{
  background-color: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  color: black;
  cursor: pointer;
  text-transform: uppercase;
}