.header {
  position: sticky;
  height: 60px;
  display: flex;
  align-items: center;
  top: 0;
  z-index: 100;
  background-color: #131921;
}
.header_logo {
  border: 1px solid transparent;
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 6px;
}
.header_logo:hover {
  border: 1px solid white;
}
.header_delivery_place {
  visibility: hidden;
}
.header_search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
}
.header_search_input {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}
.header_search_icon {
  background-color: #cd9042;
  padding: 5px;
  height: 22px !important;
}
.header_nav {
  display: flex;
  justify-content: space-evenly;
}
.header_option {
  border: 1px solid transparent;
  display: flex;
  flex-direction: column;
  padding: 6px;
  margin-left: 10px;
  margin-right: 10px;
  color: white;
}

.header_option:hover {
  border: 1px solid white;
}

.header_option_one {
  font-size: 10px;
}
.header_option_two {
  font-size: 14px;
  font-weight: 800;
}
.header_option_basket {
  border: 1px solid transparent;
  padding: 6px;
  display: flex;
  align-items: center;
  color: white;
  margin-top: 4px;
}
.header_option_basket:hover {
  border: 1px solid white;
}
.basket_count {
  margin-left: 10px;
  margin-right: 10px;
  margin-top: auto;
}
