.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  max-height: 400px;
  background-color: white;
  z-index: 1;
  color: black;
}
.product_name {
  font-weight: 600;
}
.product_rating {
  display: flex;
}
.product_image {
  border: 1px solid black;
  height: 60%;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}
.product_button {
  background-color: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: black;
  cursor: pointer;
  text-transform: uppercase;
}
.product_price {
  margin-top: 5px;
}
.product_info {
  height: 100px;
  margin-bottom: 15px;
}
