.login {
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login_logo {
  margin-top: 40px;
  margin-bottom: 20px;
  object-fit: contain;
  width: 100px;
  margin-right: auto;
  margin-left: auto;
}
.login_container {
  width: 300px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
}
.login_container_SignIn {
  font-weight: 600;
  margin-bottom: 20px;
  text-align: center;
}
.login_container_header {
  margin-bottom: 5px;
}

.login_input_field {
  width: 100%;
  height: 25px;
  border-radius: 4px;
  margin-bottom: 10px;
  background-color: white;
  border: 1px solid;
}

.login_container_para {
  margin-top: 15px;
  font-size: 12px;
}
.login_error {
  color: red;
  padding: 6px;
  display: flex;
  align-items: center;
}
.login_error_info {
  margin-left: 10px;
  margin-right: 10px;
  font-size: 14px;
}
.signInButton {
  background: #f0c14b;
  border-radius: 4px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #bebaae #846a29;
  cursor: pointer;
  text-transform: uppercase;
}

.registrationButton {
  border-radius: 4px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: darkgray;
  cursor: pointer;
  text-transform: uppercase;
}
